import React, {useState, useContext, useEffect, useCallback} from 'react'

import styled, {css} from 'styled-components'

import CardContext from 'contexts/CardContext'
import CardZone from 'components/CardZone'

const Splash = styled.div`
	width: 100vw;
	height: 100vh;
	background-color: #ffffff;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	font-family: 'B612', serif;
`;

const ZoneGrid = styled.div`
	padding: 20px;
	display: grid;
	gap: 20px;
	grid-template-areas: 
    "stats deck hand exile"
    "stats misc buff yard";
    justify-items: stretch;
    align-items: stretch;
    width: 100%;
    height: 100%;
    max-height: 100vh;
    grid-template-columns: repeat(auto-fit, minmax(0, 1fr));
    grid-template-rows: repeat(auto-fit, minmax(0, 1fr));
`;

const Square = styled.div`
	background-color: rebeccapurple;
	${props => css`
		grid-area: ${props.area || ''};
	`}
`;

const DeckTester = () => {
	const cardDefinitions = useContext(CardContext);
	const [cards, setCards] = useState();
	useEffect(() => {
		if (!cardDefinitions) return;
		setCards(
			(Object.entries(cardDefinitions) || [])
				.map(([key, value]) => [value.name, value])
				.reduce((acc, [name, val]) => Object.assign(acc, {[name]: val}), {})
		);
	}, [cardDefinitions]);
	const [zones, setZones] = useState({
		stats: 'words',
		deck: '',
		hand: '',
		exile: '',
		misc: '',
		buff: '',
		yard: '',
	});

	return (
		<Splash>
			<ZoneGrid>
				<CardZone zone="stats" name="Character Stats" value={zones.stats} cards={cards}/>
				<CardZone zone="deck" name="Deck" value={zones.deck} cards={cards}/>
				<CardZone zone="hand" name="Hand" value={zones.hand} cards={cards}/>
				<CardZone zone="exile" name="Exile" value={zones.exile} cards={cards}/>
				<CardZone zone="misc" name="Misc" value={zones.misc} cards={cards}/>
				<CardZone zone="buff" name="Buff" value={zones.buff} cards={cards}/>
				<CardZone zone="yard" name="Graveyard" value={zones.yard} cards={cards}/>
			</ZoneGrid>
		</Splash>
	);
};

export default DeckTester;